import React, { Suspense } from 'libs/react';
import { BrowserRouter as Router, Switch, Route, Link } from 'libs/react-router-dom';

import Heading from 'remote1/Heading';

const Remote = React.lazy(() => import('remote1/RemoteApp'));
const Button = React.lazy(() => import('remote1/Button'));

const App = () => {
  return (
    <Router>
      <div>
        <Suspense fallback={'loading...'}>
          <Switch>
            <Route path="/button">
              <Button />
            </Route>
            <Route path="/button">
              <Button />
            </Route>
            <Route path="/heading">
              <Heading />
            </Route>
            <Route path="/remote">
              <Remote />
            </Route>
          </Switch>
        </Suspense>
      </div>
    </Router>
  );
};

export default App;
